import axios from '@/axios.js'

//   
export async function fetchHeadData() {
  try {
    const response = await axios.get('/INSUR9101/getHead');
    return response.data;
  } catch (error) {
    console.error('   :', error);
    throw error;
  }
}

//   
export async function fetchPositions(head) {
  if (!head) return [];
  try {
    const response = await axios.get('/INSUR9101/getPosition', { params: { headcd: head } });
    return response.data;
  } catch (error) {
    console.error('   :', error);
    return [];
  }
}

//   
export async function fetchTeams(position) {
  if (!position) return [];
  try {
    const response = await axios.get('/INSUR9101/getTim', { params: { positioncd: position } });
    return response.data;
  } catch (error) {
    console.error('   :', error);
    return [];
  }
}

//   
export async function fetchJikData() {
  try {
    const response = await axios.get('/User/GET_SYS');
    return response.data.jik;
  } catch (error) {
    console.error('   :', error);
    return [];
  }
}
