<template>
    <div class="container">
         <figure style="margin-top: 0px;"><img src="@/assets/Img/login/main_logo_s.png" alt=""></figure>
         <div class="loginBox">
             <figure style="margin-top: 15px; width: 350px;"><img  style="width: 100%; " src="@/assets/Img/login/text_log01.png" alt=""></figure>
             <div class="flexBox">
                <img style="margin-bottom: 15px;" src="@/assets/Img/login/icon_key.gif" alt=""/>
                <div class="inputs">
                    <!--    -->
                    <div class="login_input">
                        <img src="@/assets/Img/login/ar_01.gif" alt=""/>
                        <img src="@/assets/Img/login/img_id.png" alt="">
                        <input style="margin-left: 15px;" type="text" @input="user_Id = $event.target.value"/>
                        <label for="save_Id_Label">
                            <input style="margin-left: 40px;" type="checkbox" id="save_Id_Label"/><span> </span>
                        </label>
                    </div>
                    <!--    -->
                    <div class="login_input">
                        <img src="@/assets/Img/login/ar_01.gif" alt=""/>
                        <img src="@/assets/Img/login/img_pw.png" alt="">
                        <input style="margin-left: 15px;" type="password" @input="user_Pw = $event.target.value"/>
                        <button style="margin-left: 40px; border: none; padding: 0;" @click="fnLogin">
                            <img src="@/assets/Img/login/bt_login.png" alt="Login" style="width: 100%; height: 100%; margin: 0;">
                        </button>
                    </div>
                </div>
             </div>
         </div>
    </div>

</template>

<style scoped>
    /* container        */
    .container {
        display: flex;
        flex-direction: column;   /*   */
        justify-content: center;  /*    */
        align-items: center;      /*    */
        height: 100vh;            /*  100vh  */
        width: 100%;
    }

    /* loginBox container   */
    .container .loginBox {
        display: flex;               /* flexbox  */
        flex-direction: column;      /*   */
        align-items: center;
        height: 350px; /*    */
        width: 900px; /*    */
        border: 1px solid black;
        background-image: url('../assets/Img/login/log_tb_bg.png');
        background-size: 100% 100%; /*        */
        background-position: center; 
        background-repeat: no-repeat; 
    }

    .flexBox {
        display: flex;               /* flexbox  */
        flex-direction: row;         /*   */
        justify-content: center;     /*    */
        align-items: center;         /*    */
        width: 100%;
        margin-right: 50px;
        margin-top: 30px;
    }

    .inputs {
        display: flex;
        flex-direction: column;      /*   */
        align-items: flex-start;     /*   */
        margin-left: 10px;            /*    */
    }

    .login_input {
        display: flex;
        flex-direction: row;
        align-items: center; /*    */
        margin-bottom: 15px;  /*     */
    }

    .login_input img {
        height: 15px;
        margin-right: 10px;  /*     */
    }

   
</style>
<script>
    import '../assets/Css/main.css'; 
    import axios from '@/axios.js'; // axios  
    import { ref } from 'vue';
    export default{
        name : "loginPageViews",
        setup(props, { emit }){
            const user_Id = ref('');
            const user_Pw = ref('');

            async function fnLogin(){
                if(user_Id.value === ''){
                    alert('ID .')
                    return;
                }

                if (user_Pw.value === '') {
                    alert(' .')
                    return;
                }

                try{
                    const response = await axios.post('/login/login', {
                        user_Id : user_Id.value,
                        user_Pw : user_Pw.value
                    }, { withCredentials: true });   
                    //     
                    const token = response.data.token;
                    localStorage.setItem('auth_token', token);
                    //   
                    alert(' .');

                    //    
                    emit('login_success');
                } catch(error){
                    alert(error.response.data.Message);
                }
            }

            return {
                user_Id,
                user_Pw,
                fnLogin,
            };
        }
    }
</script>
