<template>
    <div class="box10">
      <div style="overflow: hidden;">
        <!--   -->
        <div v-for="(Menu, index) in menu_List" :key="Menu.MENU_ERPCD">
          <a 
            @click="toggleSubMenu(index, Menu.MENU_ERPCD)"
            :class="{subOpen : subMenuActive === Menu.MENU_ERPCD}"
          >
            {{ Menu.MENU_ERPNM }}</a>
          <!-- :      -->
          <div v-if="isSubMenuVisible(index)" class="submenu">
            <a @click="SubMenuClickFunction(subMenu.DETAILNAME, subMenu.MENUPATH, subMenu.DETAILCODE, menu_List, index, i)" 
               :class="{ subsubOpen: subsubMenuActive === subMenu.DETAILCODE }"
               v-for="(subMenu, i) in Menu.SUB_MENUS" :key="i" 
            >
              {{ subMenu.DETAILNAME }}
            </a>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { ref, watch, watchEffect, nextTick } from 'vue';
  
  export default {
    name: "SidebarComponent",
    props: {
      menuData: {
        type: Array,
        default: () => [],
      },
      sideBar_Bool: {
        type: Boolean,
        default: false,
      },
      subIndex: {
        type: Number, 
        default: -1,
      },
      subSubIndex: {
        type: Number, 
        default: -1,
      },
    },
    setup(props, {emit}) {
      const menu_List = ref(props.menuData); // menuData   
      const currentSubMenuIndex = ref(null); //     
      const subMenuActive = ref(null);
      const subsubMenuActive = ref(null);
  
      //    
      const toggleSubMenu = (index, menuCode) => {
        //       
        if (currentSubMenuIndex.value === index) {
          //     
          currentSubMenuIndex.value = null;
          subMenuActive.value = null;
          subsubMenuActive.value = null;
        } else {
          //      ,   
          currentSubMenuIndex.value = index;
          subMenuActive.value = menuCode;
          subsubMenuActive.value = null;
        }
      };
  
      //     
      const isSubMenuVisible = (index) => {
        return currentSubMenuIndex.value === index;
      };

      function SubMenuClickFunction(SubMenuName, path, subMenuCode, menuData, subIndex, subSubIndex){
        subsubMenuActive.value = subMenuCode;
        emit('SidebarSubMenuClick', {SubMenuName ,path, menuData, subIndex, subSubIndex});
      }

      // menuData   menu_List 
      watch(() => props.menuData, (newMenuData) => {
        currentSubMenuIndex.value = null;
        subMenuActive.value = null;
        subsubMenuActive.value = null;
        menu_List.value = newMenuData;
      });

      watchEffect(async () => {
        console.log("props.subIndex:", props.subIndex);
        console.log("props.subSubIndex:", props.subSubIndex);
        console.log("menu_List:", menu_List.value);

        //   null  watchEffect   
        currentSubMenuIndex.value = null;
        subMenuActive.value = null;
        subsubMenuActive.value = null;

        await nextTick(); //  Vue DOM    

        //  `?.`( ) `undefined` 
        currentSubMenuIndex.value = props.subIndex;
        subMenuActive.value = menu_List.value[props.subIndex]?.MENU_ERPCD || null;
        subsubMenuActive.value = menu_List.value[props.subIndex]?.SUB_MENUS?.[props.subSubIndex]?.DETAILCODE || null;
    });
          
      
  
      return {
        menu_List,
        subMenuActive,
        subsubMenuActive,
        toggleSubMenu,
        isSubMenuVisible,
        SubMenuClickFunction,
      };
    }
  }
  </script>
  
  <style lang="scss" scoped>
  .box10 {
    background-color: #fdfdfd;
    /* border-top: 1px solid black; */
    position: absolute;
    margin: 0;
    top: 0px;
    bottom: 0px;
    width: 300px;
    color: black;
  }
  
  .box10 div {
    width: 100%;
  }
  
  .box10 a {
    width: 100%;
    background-color: transparent;
    text-align: left;
    overflow: hidden;
    cursor: pointer;
    height: 40px;
    font-size: 20px;
    text-decoration: none;
    display: flex;
    align-items: center; /*    */
    @extend .basic_font_Bold;
  }

  // .box10 a.subOpen {
  //   background-color: lightgray;
  //   @extend .basic_font;
  // }
  
  
  /*   */
  .submenu {
    background-color: transparent;
    padding-left: 10px;
    width: 100%;
    margin-left: 10px; /*    */
  }
  
  .submenu a {
    font-size: 20px;
    background-color: transparent;
    padding-left: 10px;
    display: flex;
    align-items: center; /*    */
    width: 100%;
    @extend .basic_font_Bold;
  }

  /*  (active)  */
  // .submenu a.subsubOpen {
  //     background-color: lightgray; /*   */
  //     @extend .basic_font;
  // }
  </style>
  