  top: window.innerHeight / 2 - 390, 
  left: window.innerWidth / 2 - 450,
});
let drag = { active: false, offsetX: 0, offsetY: 0 };


//
const closeModal = () => {
  emit("update:isOpen", false);
  console.log('');
};

//  
const startDrag = (event) => {
  drag.active = true;
  drag.offsetX = event.clientX - modalRef.value.getBoundingClientRect().left;
  drag.offsetY = event.clientY - modalRef.value.getBoundingClientRect().top;

  window.addEventListener("mousemove", onDrag);
  window.addEventListener("mouseup", stopDrag);
};

//  
const onDrag = (event) => {
  if (!drag.active) return;
  position.value.top = event.clientY - drag.offsetY;
  position.value.left = event.clientX - drag.offsetX;
};

//  
const stopDrag = () => {
  drag.active = false;
  window.removeEventListener("mousemove", onDrag);
  window.removeEventListener("mouseup", stopDrag);
};


//   
watch (
  () => props.sawonData,
  (newVal) => {
    localDate.value = {...newVal}
  },
  { deep: true,immediate: true }
)

//    (isOpen true  )    
watch(() => props.isOpen, async (newVal) => {
  const tempPosition = localDate.value.selectedPosition;
  const tempTim = localDate.value.selectedTim;
  if (newVal) {
    //   selectedHead     
    if (localDate.value.selectedHead !=='') {
      try {
        const posResponse = await axios.get('/INSUR9101/getPosition', {
          params: { headcd: localDate.value.selectedHead }
        });
        positionList.value = posResponse.data;
      } catch (error) {
        console.error("   :", error);
      }
    }
    localDate.value.selectedPosition = tempPosition;
    //   selectedPosition     
    if (localDate.value.selectedPosition !=='') {
      try {
        const timResponse = await axios.get('/INSUR9101/getTim', {
          params: { positioncd: localDate.value.selectedPosition }
        });
        timList.value = timResponse.data;
      } catch (error) {
        console.error("   :", error);
      }
    }
    localDate.value.selectedTim = tempTim;
  }
});

//      
watch(()=>localDate.value.selectedHead, async (newHead) => {
  localDate.value.selectedPosition = ''
  positionList.value = []
  localDate.value.selectedTim = ''
  timList.value = []

  if (newHead) {
    try {
      const response = await axios.get('/INSUR9101/getPosition', {
        params: { headcd: newHead }
      })
      positionList.value = response.data
    } catch (error) {
      console.error('   :', error)
    }
  }
})

//       
watch(()=>localDate.value.selectedPosition, async (newPosition) => {
  localDate.value.selectedTim = ''
  timList.value = []

  if (newPosition) {
    try {
      const response = await axios.get('/INSUR9101/getTim', {
        params: { positioncd: newPosition }
      })
      timList.value = response.data
    } catch (error) {
      console.error('   :', error)
    }
  }
})


// api
function showApi(){
  new window.daum.Postcode({
    //   
    oncomplete: (data) => {
      //data.roadAddress( )    .
      let fullRoadAddr = data.roadAddress;
      let extraRoadAddr = '';
      console.log('api ')
      //data.bname data.buildingName    . ex) bname = () buildingName()
      if (data.bname !== '' && /[||]$/g.test(data.bname)) {
        extraRoadAddr += data.bname;
      }
      if (data.buildingName !== '' && data.apartment === 'Y') {
        extraRoadAddr += (extraRoadAddr !== '' ? ', ' + data.buildingName : data.buildingName);
      }
      if (extraRoadAddr !== '') {
        extraRoadAddr = ' (' + extraRoadAddr + ')';
      }
      if (fullRoadAddr !== '') {
        fullRoadAddr += extraRoadAddr;
      }

      // 
      localDate.value.ZIP = data.zonecode;

      // 
      localDate.value.ADDR = fullRoadAddr;
    }
  }).open();
} 

//InDuceModal  (,)
function updateInduce(data){
  localDate.value.INDUCE_NM = data.INDUCE_NM;
  localDate.value.INDUCE_CD = data.INDUCE_CD;
}

//
function validate_Sawon_Info(){
  validate_msg.value = ''
