import * as xlsx from 'xlsx';
import { saveAs } from 'file-saver';

export function exportToExcel({rowData,columDefs,fileName = 'data.xlsx'}){
  //
  const header = columDefs.map(col => col.headerName);
  const fields = columDefs.map(col => col.field);

  //row data fied  
  const data = rowData.map(item => fields.map(field => item[field]));

  //   
  data.unshift(header);

  //worksheet
  const ws = xlsx.utils.aoa_to_sheet(data);

  //!cols =SheetJS( xlsx )    .
  //SheetJS       , !    .
  //!cols           .
  ws['!cols'] = columDefs.map(col => col.width ? { wpx: col.width } : undefined);

  //workbook
  const wb = xlsx.utils.book_new();
  xlsx.utils.book_append_sheet(wb, ws, 'Sheet1')

  //wokrbook binary    
  const workbookOut =xlsx.write(wb,{bookType:'xlsx',type:'array'});
  saveAs(new Blob([workbookOut], { type: 'application/octet-stream' }), fileName)
}