import axios from '@/axios.js'

//     (props   )
export async function fetchUserAuth(menuGrpcd, menuErpcd, menuDtlcd, authCode) {
  try {
    const response = await axios.get('User/User_Auth', {
      params: {
        MENU_GRPCD: menuGrpcd,
        MENU_ERPCD: menuErpcd,
        MENU_DTLCD: menuDtlcd,
        AuthCode: authCode
      },
      withCredentials: true
    });
    
    return Boolean(response.data) && response.data !== " .";
  } catch (error) {
    console.error(` (${authCode})  :`, error);
    return false;
  }
}

//     
export async function fetchUserDetailAuth(menuGrpcd, menuErpcd, menuDtlcd) {
  try {
    const response = await axios.get('User/User_Auth_Detail', {
      params: {
        MENU_GRPCD: menuGrpcd,
        MENU_ERPCD: menuErpcd,
        MENU_DTLCD: menuDtlcd
      },
      withCredentials: true
    });
    return response.data;
  } catch (error) {
    console.error("     :", error);
    return null;
  }
}
