<template>
    <router-view @login_success="login_success"/>
</template>

<script>
  import { onMounted} from 'vue';
  import { useRouter } from 'vue-router';
  import axios from '@/axios.js'

export default {
  name: 'App',
  setup(){
    const router = useRouter();

    //     
    const checkSession = async () => {
      try {
        const response = await axios.get('/login/checklogin', { withCredentials: true });
        if (response.data.isAuthenticated) {
          router.push({ path: "/Index" }); //    
        } else {
          router.push({ name: 'loginPageView' }); //      
        }
      } catch (error) {
        console.error("  :", error);
        router.push({ name: 'loginPageView' });
      }
    };

    //       
    onMounted(() => {
      checkSession();
    });

    //      
    function login_success() {
      console.log("  !");
      router.push({ path: "/Index" }); //    
    }
    
    return{
      login_success,
    }
  }
}
</script>

<style>

  html {
    overflow-y: scroll; /*        */
  }
  html, body {
      height: 100%;  /*      */
      margin: 0;     /*    */
      padding: 0;
      top: 0;
      left: 0;
      box-sizing: border-box;  /*     */
  }
</style>
