<template>
    <div>
        <div class="Nav_Container flex_Center">
            <div class="flex_Left" style="margin-left: 30px; width: 2400px; position: relative;">
                <h1>SKY</h1><p style="margin-top: 25px; margin-right: 5px;">Blue</p><h1>ASSET</h1>
                <div class="flex_Center" style="margin: 0px 0px 0px 40px; width: 2000px;">
                    <div 
                        @mouseenter="showSubMenu(menu.MENU_GRPCD)"
                        @mouseleave="hideSubMenu(menu.MENU_GRPCD)"
                        :style="{ width : Menu_Width + '%' }" 
                        v-for="(menu, i) in Menu_Data" 
                        :key="menu.MENU_GRPCD"
                    >
                        <button @click="menuClickFunction(i)" class="Menu">
                            {{ menu.MENU_GRPNM }}
                        </button>
                    </div>
                </div>
                 <!--       SubMenu -->
            <!-- <div class="SubMenu" v-if="activeMenu">
                <div 
                    v-for="(SubMenu, e) in Menu_Data.find(menu => menu.MENU_GRPCD === activeMenu)?.MENUS" 
                    :key="SubMenu.MENU_ERPCD" 
                    class="SubMenuItem"
                    @mouseenter="showSubSubMenu(SubMenu.MENU_ERPCD)"
                    @mouseleave="hideSubSubMenu(SubMenu.MENU_ERPCD)"
                >
                    {{ SubMenu.MENU_ERPNM }}
                    <div class="SubSubMenu" v-if="activeSubMenu === SubMenu.MENU_ERPCD">
                        <div 
                            v-for="(subSub, q) in SubMenu.SUB_MENUS" 
                            :key="subSub.MENU_DTLCD" 
                            class="SubSubMenuItem"
                            @click="SubMenuClickFunction(i, e, q, subSub)"
                        >
                            <p>{{ subSub.DETAILNAME }}</p>
                        </div>
                    </div>
                </div>
            </div> -->
            </div>
        </div>
    </div>
</template>

<script>
    import { ref, onMounted } from 'vue';
    import axios from 'axios';

    export default {
        name : "NavbarComponent",
        setup(props, { emit }){ 
          const Menu_Data = ref('');
          const Menu_Width = ref('');
          const activeMenu = ref(null);
          const activeSubMenu = ref(null);
          
          onMounted(async () => {
                try{
                    const response = await axios.get('http://localhost:5062/api/MenuPageGetData',{ withCredentials: true });
                    
                    Menu_Data.value = response.data;
                    Menu_Width.value = 100 / Menu_Data.value.length; 
                    console.log(Menu_Data.value);
                    console.log(response.data);
                } catch(error){
                    alert(error.response.data.message);
                }
            });
            const returnSideMenu = ref([]);
            
            function menuClickFunction(i){
                returnSideMenu.value = Menu_Data.value[i].MENUS;
                emit('NavbarmainMenuClick', {returnSideMenu});
            }

            function SubMenuClickFunction(mainindex ,subIndex, subSubIndex, subSubList){
                returnSideMenu.value = Menu_Data.value[mainindex].MENUS;
                const SubMenuName = subSubList.DETAILNAME;
                const Path = subSubList.MENUPATH;
                emit('NavbarSubMenuClick', {returnSideMenu,subIndex, subSubIndex, SubMenuName, Path});
            }

            function showSubMenu(menuId) {
                activeMenu.value = menuId;
            }

            function hideSubMenu(menuId) {
                if (activeMenu.value === menuId) activeMenu.value = null;
            }

            function showSubSubMenu(subMenuId) {
                activeSubMenu.value = subMenuId;
            }

            function hideSubSubMenu(subMenuId) {
                if (activeSubMenu.value === subMenuId) activeSubMenu.value = null;
            }

            return{
                menuClickFunction,
                returnSideMenu,
                Menu_Data,
                Menu_Width,
                showSubMenu,
                hideSubMenu,
                showSubSubMenu,
                hideSubSubMenu,
                activeMenu,
                activeSubMenu,
                SubMenuClickFunction
            };
        }
    }
</script>
<style lang="scss" scoped>
    .Nav_Container{
        width: 100%;
        height: 80px;
        border: 1px solid #ccc;
        margin-top: 15px;
        background-color: rgb(20, 20, 165);
        color: white;
    }

    .Menu {
        background-color: transparent;
        padding: 22px 20px;
        font-size: 22px;
        border: none;
        cursor: pointer;
        width: 100%;
        color: white;
        @extend .basic_font_Bold;
    }

    .SubMenu {
        position: absolute;  /*      */
        top: 80px;        /*      */
        left: 0;          /*     */
        background: white;
        color: black;
        border: 1px solid #ccc;
        z-index: 1000;
        font-size: 24px;
        width: 2400px; /*    */
    }

    .SubMenuItem {
        padding: 10px;
        cursor: pointer;
        position: relative;
        @extend .basic_font;
    }

    .SubMenuItem:hover {
        background: lightgray;
    }

    .SubSubMenu {
        position: absolute;
        left: 100%;
        top: 0;
        color: black;
        min-width: 400px;
        border: 1px solid #ccc;
        z-index: 1001;
        @extend .basic_font;
    }

    .SubSubMenu p {
        font-size: 22px;
        margin-top: 5px;
        margin-bottom: 10px;
        position: relative; /*         */
        z-index: 1;
    }

    .SubSubMenu p::before {
        content: "";
        position: absolute;
        top: -5px; /* margin-top    */
        left: 0;
        right: 0;
        bottom: -10px; /* margin-bottom    */

        z-index: -1; /*      */
        opacity: 0; /*   */
        transition: opacity 0.2s ease-in-out;
    }

    .SubSubMenu p:hover::before {
        opacity: 1; /* hover     */
    }


</style>