<template>
  <div ref="gridContainer" class="tui-grid-wrapper"></div>
</template>

<script>
import { ref, onMounted, watch, nextTick } from "vue";
import Grid from "tui-grid";
import "tui-grid/dist/tui-grid.css";

//    
Grid.applyTheme("striped");

export default {
  name: "TuiGridWrapper",
  props: {
    data: {
      type: Array,
      required: true
    },
    columns: {
      type: Array,
      required: true
    },
    bodyHeight: {
      type: Number,
      default: 400
    }
  },
  setup(props) {
    const gridContainer = ref(null);
    const gridInstance = ref(null);
    const isGridInitialized = ref(false); //  Grid  

    //  Grid  
    const createGrid = () => {
      if (!gridContainer.value) {
        console.error(" Grid   !");
        return;
      }
      if (!props.columns.length) {
        console.warn("   . Grid  ...");
        return;
      }

      console.log(" Grid  ");

      gridInstance.value = new Grid({
        el: gridContainer.value,
        data: props.data || [],
        columns: props.columns.map(col => ({ ...col, headerText: col.header || col.headerText })), //  header  
        bodyHeight: props.bodyHeight,
        header: { height: 60 }, //    
      });

      isGridInitialized.value = true; //  Grid  
      console.log(" Grid  :", gridInstance.value);
    };

    //  Grid   (  )
    const updateGridColumns = () => {
      if (!gridInstance.value) return;

      nextTick(() => {
        console.log(" Grid   :", JSON.stringify(props.columns, null, 2));

        gridInstance.value.setColumns(props.columns.map(col => ({ ...col, headerText: col.header || col.headerText }))); //  header  
        gridInstance.value.refreshLayout(); //   
      });
    };

    //  Grid    (  )
    const updateGridData = () => {
      if (!gridInstance.value) return;

      nextTick(() => {
        console.log(" Grid   ,  ");
        gridInstance.value.resetData([...props.data]); //    
        gridInstance.value.refreshLayout(); //   
      });
    };

    //  `onMounted()`   Grid 
    onMounted(() => {
      nextTick(() => {
        console.log("   :", JSON.stringify(props.columns, null, 2));

        if (props.columns.length > 0) {
          createGrid();
        } else {
          console.warn("    Grid  .");
        }
      });
    });

    //  `columns`  Grid  (or )
    watch(
      () => props.columns,
      (newColumns) => {
        console.log("   :", JSON.stringify(newColumns, null, 2));

        if (!newColumns.length) {
          console.warn("   Grid  .");
          return;
        }

        if (!isGridInitialized.value) {
          console.log(" Grid    ...");
          createGrid();
        } else {
          console.log("  Grid  ");
          updateGridColumns();
        }
      },
      { immediate: true, deep: true }
    );

    //  `data`  Grid  (  )
    watch(
      () => props.data,
      (newData, oldData) => {
        if (!isGridInitialized.value) return;

        if (JSON.stringify(newData) !== JSON.stringify(oldData)) { //     
          updateGridData();
        } else {
          console.log("   -  ");
        }
      },
      { deep: true }
    );

    return { gridContainer };
  }
};
</script>

<style lang="scss">
.tui-grid-wrapper {
  width: 100%;
  height: 400px;
  min-height: 300px;
}

/*  Grid     */
.tui-grid-container {
  font-size: 20px !important;
  font-family: 'HanSantteutDotum-Bold' !important;
  src: url('@/assets/fonts/HanSantteutDotum-Bold.ttf') format('truetype') !important;
  font-weight: normal !important;
  font-style: normal !important;
}

/*  Grid     */
.tui-grid-header th {
  font-size: 20px !important;
  width: 150px !important;
  height: 50px !important; 
}

/*  Grid      */
.tui-grid-body td {
  font-size: 20px !important;
  width: 150px !important;
  height: 50px !important;
}

</style>
