import { ref} from 'vue';
import { fetchUserAuth, fetchUserDetailAuth } from '@/api/authService';


/*0~5 USER_DETAIL
0 : 
1 : 
2 : 
4 : 
5 : */ 
/*useAuth (, , ) */
export function useAuth(menuGrpcd, menuErpcd, menuDtlcd) {
  const ExcelAuth = ref(false);
  const WriteAuth = ref(false);
  const DelteAuth = ref(false);
  const PrintAuth = ref(false);
  const USER_DETAIL = ref(null);
  const sessionData = ref({
    head: '',
    position: '',
    tim: '',
    saknm: ''
  });

  //   
  async function loadAuth() {
    //  Excel  
    ExcelAuth.value = Boolean(await fetchUserAuth(menuGrpcd, menuErpcd, menuDtlcd, "E"));

    //  Write  
    WriteAuth.value = Boolean(await fetchUserAuth(menuGrpcd, menuErpcd, menuDtlcd, "W"));

    //  Delete  
    DelteAuth.value = Boolean(await fetchUserAuth(menuGrpcd, menuErpcd, menuDtlcd, "W"));

    //  Print  
    PrintAuth.value = Boolean(await fetchUserAuth(menuGrpcd, menuErpcd, menuDtlcd, "W"));

    const userDetail = await fetchUserDetailAuth(menuGrpcd, menuErpcd, menuDtlcd);
    if (userDetail) {
      USER_DETAIL.value = userDetail.USER_DETAIL;
      Object.assign(sessionData.value, {
        head: userDetail.HEAD_CD,
        position: userDetail.POSITION_CD,
        tim: userDetail.TIM_CD,
        saknm: userDetail.SA_KNM
      });
    }
  }

  return {
    ExcelAuth,
    WriteAuth,
    DelteAuth,
    PrintAuth,
    USER_DETAIL,
    sessionData,
    loadAuth
  };
}
